import React, { useEffect, useState } from 'react';
import s from './Pagination.module.scss';
import ArrowSvg from 'assets/svg/chevron-right.svg';

export type paginationType = {
  page: number;
  limit: number;
};

type propsType = {
  pagination: paginationType;
  setPagination: (pagination: paginationType) => void;
  totalCount: number;
};

const Pagination: React.FC<propsType> = (props) => {
  const { pagination, setPagination, totalCount } = props;

  const [numberButton, setNumberButton] = useState<Array<number>>([]);

  const buttonsArray = numberButton.map((item, idx) => {
    if (item > 0) {
      return (
        <button
          key={idx}
          onClick={() => changePage(item)}
          className={s.paginationPageButton}
          style={{
            borderBottom: pagination.page === item ? '2px solid var(--PrimaryBlue)' : 'none',
            color: pagination.page === item ? 'var(--Black)' : 'var(--GreyText)',
          }}
        >
          {item}
        </button>
      );
    } else {
      return (
        <span key={idx} className={s.spacePoint}>
          . . .
        </span>
      );
    }
  });

  const getPaginationList = (pagination: paginationType, totalCount: number) => {
    const pagesCount = Math.ceil(totalCount / pagination.limit);

    const tempArr: number[] = [];

    tempArr.push(1);

    if (pagination.page > 5) {
      tempArr.push(-1);
    }

    for (let i = 2; i < pagesCount; i++) {
      if (pagination.page - 4 < i && pagination.page + 4 > i) {
        tempArr.push(i);
      }
    }

    if (pagination.page < pagesCount - 4) {
      tempArr.push(-1);
    }

    if (pagesCount > 1) {
      tempArr.push(pagesCount);
    }

    return tempArr;
  };

  const changePage = (page: number) => {
    setPagination({ ...pagination, page });
  };

  useEffect(() => {
    setNumberButton(getPaginationList(pagination, totalCount));
  }, [pagination, totalCount]);

  return (
    <div className={s.content}>
      <button
        className={`${
          pagination.page === 1 ? s.pageSwitchDisabled : s.pageSwitchActive
        }`}
        onClick={() => changePage(pagination.page - 1)}
        disabled={pagination.page === 1}
      >
        <ArrowSvg
          style={{ rotate: '180deg' }}
        />
      </button>
      {buttonsArray}
      <button
        className={`${
          pagination.page === numberButton.pop() ? s.pageSwitchDisabled : s.pageSwitchActive
        }`}
        onClick={() => changePage(pagination.page + 1)}
        disabled={ pagination.page === Math.ceil(totalCount / pagination.limit) }
      >
        <ArrowSvg/>
      </button>
    </div>
  );
};

export default Pagination;
